* header {
  box-sizing: border-box;
}

header {
  font-family: Roboto;
  background-color: transparent;
  border-radius: 0 0 5px 5px;
  width: 100%;
  height: 60px;
  padding: 0 20px;
  position: fixed;
  color: #000;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  z-index: 99;
}

header h2 {
  font-size: 1.6em;
  font-weight: lighter;
  margin: 0;
}
header h2:hover {
  cursor: pointer;
}

header ul {
  margin: 0px;
  padding: 0;
  font-size: 20px;
}

header ul li {
  padding: 2px 5px;
  cursor: pointer;
  font-size: 1.1em;
  list-style: none;
}

header ul li:hover,
header ul li a:hover {
  background-color: #abcdef;
  border-radius: 5px;
  text-shadow: none;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

header a {
  color: inherit;
  text-decoration: none;
}

header a:visited {
  text-decoration: none;
  color: inherit;
}

