}

.side-drawer a {
  color: #111;
  text-decoration: none;
  font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: #abcdef;
}


@media (min-width: 750px) {
    .side-drawer {
        display: none;
    }
}

