.contact-container {
  font-family: Roboto;
  padding: 130px 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-header {
  display: flex;
  flex-direction: column;
  align-items: ;
}

.contact-header h3 {
  font-size: 2em;
  padding-bottom: 1em;
}

.form-container {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.form-control {
  width: 100%;
  margin: 1.2em 0;
  font-size: 1rem;
  line-height: 2.25;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
}

.contact-container textarea {
  height: 8em;
}

#submit-button {
  padding: 0.5rem 1rem;
  font-size: 1.25rem;
  line-height: 1.5;
  border-radius: 0.5rem;
  background-color: #111;
  color: #fff;
  cursor: pointer;
  font-weight: 600;
  border: none;
}

.btnDiv {
  text-align: center;
}
