.blog-container {
  min-height: 80vh;
  max-width: 100%;
  text-align: center;
  padding: 5rem 3%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.blog-sidebar {
  background-color: #012233;
  color: #fff;
  border-radius: 5px 0 0 5px;
  padding: 1.5em 1em;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.blog-sidebar h2 {
  margin-bottom: 1.5em;
  text-decoration: none;
}

.blog-sidebar ul li {
  list-style: none;
  opacity: 0.8;
  margin: 0.5em;
}

.blog-sidebar ul li:hover {
  list-style: none;
  opacity: 1;
}

.blog-sidebar a,
.blog-sidebar a:visited {
  text-decoration: none;
  color: #fff;
}

.blog-sidebar a:hover,
.blog-sidebar a:active {
  /* text-decoration: underline; */
}

.article-container {
  background-color: white;
  border-radius: 0 5px 5px 0;
  flex: 6 1 0;
  padding: 2em 5%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
  /* margin: 0 2em; */
}

.article-container h1 {
  margin-bottom: 1em;
  max-width: 100%;
}

.article-container p {
  line-height: 24px;
}

.article-container img {
  max-width: 60%;
  max-height: 60%;
  padding: 1em;
  margin: 2em;
  border: 4px solid #012233;
  border-radius: 1em;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.about-img-wrapper img {
  max-width: 300px;
  max-height: 300px;
  padding: 0;
  border: 4px solid #012233;
  border-radius: 50%;
  max-width: 100%;
}

@media all and (max-width: 700px) {
  .about-img-wrapper img,
  .article-container img {
    margin: 2em 0;
    padding: 0;
  }

  .blog-sidebar {
    margin: 1em;
  }
}
